.fact-card{
    width: 100%;
    height: 100%;
    padding:  10px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.445);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotateY(calc(var(--p)*45deg)) translateZ(400px);
            transform: rotateY(calc(var(--p)*45deg)) translateZ(400px);
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    background-size: cover;
    background-repeat: no-repeat;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    background-image: linear-gradient(rgba(0, 0, 0, 0.288), rgba(0, 0, 0, 0.836));
}
.fact-card p{
    font-family: 'Aldrich', sans-serif;
    margin-bottom: 0;

}

.fact-card h3{
    text-align: center;
    padding: 10px 15px;
    background-color: #da8c50;
    background-color: brown;
    color: white;
    border-radius: 100%;
    position: absolute;
    margin-bottom: 10px;
    top: -20px;
}

.fact-card h4{
    margin-top: 20px;
}
@media screen and (max-width : 400px){
    .fact-card{
        padding: 0 10px;
    }
}

*{
    margin : 0;
    padding : 0;
    box-sizing: border-box;
    font-family: 'Aldrich';
}
section{
    padding: 20px;
}

.heading{
    font-family: monospace;
    text-align: center;
    color: white;
    background-color: brown;
    padding: 10px 0;
    text-transform: uppercase;
}


/* Facts  section */


#factBox{
    padding: 30px;
    background-color: #1e272e; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    -webkit-perspective: 1300px;
            perspective: 1300px;
    height: 95vh;
    overflow: hidden;
}

.box{
    height:280px;
    width: 250px;
    display: flex;
    align-items: center;
    position: relative;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: 1s;
    cursor: grab;
    -webkit-transform: perspective(11900px) rotateY(40deg);
            transform: perspective(11900px) rotateY(40deg);
}

@media screen and (max-width : 400px) {
    section img{
        height: auto;
    }

   #factBox{
    -webkit-perspective: 3000px;
            perspective: 3000px;
    height: 85vh;

   }
    .box{
        height: 47%;
        width: 80%;
    }

}

@media screen and (max-width : 370px) {
    .box{
        height: 50%;
        width: 85%;
    }
}

@media screen and (max-width : 300px) {
    .box{
        height: 55%;
        width: 95%;
    }

}


/* APOD section */

#APOD{
    width: 100%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.288), rgba(0, 0, 0, 0.836)), url("https://wallpapercave.com/wp/wp2461878.jpg");
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgb(226, 226, 226);
}

.noData{
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#APOD img{
    max-width: 100%;
    max-height: 500px;
    display: block;
    margin: 20px auto;
    object-fit: cover;
    border-radius: 10px;
}

.apodMedia{
    max-width: 100%;
    margin: 20px 0;
    aspect-ratio: 1/1;
    height: 500px;
    border-radius: 10px;
}

#APOD h3{
    text-align: center;
}
#APOD h3::first-letter{
    font-size: 200%;
    line-height: 1.8;

}

#APOD p{
    text-align: center;
    margin-top: 10px;
    width: 100%;
    font-size: 20px;
    line-height: 1.8;
    letter-spacing: 1px;
}

#APOD p::first-letter{
    font-weight: bolder;
}

@media screen and (max-width : 500px) {
    #APOD p{
        width: 100%;
    }
    #APOD img{
        width: 100%;
        aspect-ratio: 1;
    }
}

